	module.exports = {"data":{"post":{"html":"<p>I haven't seen this explicitly stated anywhere, but you probably don't want to run <a title=\"mysqlsnapshot\" href=\"http://jeremy.zawodny.com/mysql/mysqlsnapshot/\">mysqlsnapshot</a> on a database that is already an active master, since the script issues a RESET MASTER which will stop replication in its tracks. </p>\n<p>Another good hint for mysql is to make sure that the mysql user has write privileges in its data directory. That's such a basic mistake that you don't even think it could go wrong, but it can.</p>","fields":{"title":"SELECT * FROM error;","date":"15 Feb 2005","url":"https://papascott.de/2005-02-15-select-from-error/"}}},"pathContext":{"slug":"/2005-02-15-select-from-error/","prev":{"fields":{"title":"Schockwellenreiter RSS","date":"11 Feb 2005","slug":"/2005-02-11-schockwellenreiter-rss/"}},"next":{"fields":{"title":"C64, Where Are You?","date":"16 Feb 2005","slug":"/2005-02-16-c64-where-are-you/"}}}}

